<template>
<div v-for="items in list" :key="items.index" >
  <div class="h-full">
    <figure>
    <a href="#" class="link">
    <v-lazy-image @error="notShow" :src="`${items.slider[`${nums}`].news_image[0].url}`" :style="{height:myHeight+'px'}" class='w-full  transition-all duration-300 cursor-pointer sm:h-24 md:h-28 hover:scale-110'  alt=""/>
    </a>
    </figure>
    <p class=" paragraph text-black text-xs sm:text-xs  text-start font-semibold  mt-0.5 " :style="{fontSize:myFont+'px',lineHeight:lineFont+'px'}">
      {{items.slider[`${nums}`].title}}
    </p>
    <div class="flex flex-row align-center justify-between p-1">
      <div><span class="text-black font-thin text-xs sm:text-xs lg:text-xs lg:visible sm:invisible">2 ساعت قبل</span></div>
      <div class="flex flex-row justify-between">
        <div class="ml-1 flex">
          <span class="text-black font-thin text-xs sm:text-xs lg:visible lg:text-xs sm:invisible">{{items.slider[`${nums}`].visit}}</span>
          <font-awesome-icon :icon="['fas','eye']" class="text-black font-thin text-xs lg:text-xs sm:text-xs lg:visible sm:invisible"></font-awesome-icon>
        </div>
        <div class="flex">
          <span class="text-black font-thin p-0.5 text-xs lg:visible lg:text-xs sm:invisible">{{items.slider[`${nums}`].news_comment_count}}</span>
          <font-awesome-icon :icon="['fas','comment']" class="text-black text-xs lg:text-xs sm:text-xs lg:visible sm:invisible"></font-awesome-icon>
        </div>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import{FontAwesomeIcon} from "@fortawesome/vue-fontawesome";
import VLazyImage from 'v-lazy-image'
import{ref} from 'vue'
export default {
  name: "GridSlider",
  components:{FontAwesomeIcon,VLazyImage},
  props:{
    list:{
      type:Array,
      required:true,
      default:null
    },
    nums:{
      type:Number,
      required:true,
      default: null
    },
    myHeight:{
      type:Number,
      required:true,
      default:null
    },
    myFont:{
      type:Number,
      required:true,
      default:null
    },
    lineFont:{
      type:Number,
      required:true,
      default:null
    }
  },
  setup(){
    const notFound=ref('https://footballi.net/assets/shared-application/images/bg.svg')
    function notShow(event){
      event.target.src=notFound.value
    }
    return{
   notFound,notShow
    }
  }
}
</script>

<style scoped>
figure{
  overflow: hidden;
}
</style>